module.exports = "/*\n00 - 09: Normal\n10 - 19: Project Feature Layers\n20 - 29: Overlays\n30 - 39: Headers/Titles\n40 - 49:\n50 - 59:\n60 - 69:\n70 - 71:\n80 - 89:\n90 - 99: Animations\n*/\n/*\n00 - 09: Normal\n10 - 19: Project Feature Layers\n20 - 29: Overlays\n30 - 39: Headers/Titles\n40 - 49:\n50 - 59:\n60 - 69:\n70 - 71:\n80 - 89:\n90 - 99: Animations\n*/\n.background {\n  transition: all 400ms ease-in-out;\n  width: 100%;\n  height: 200px;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover; }\n.background .ascii-wrapper {\n    transition: all 400ms ease-in-out;\n    transition-duration: 600ms;\n    height: 100%;\n    background-color: rgba(0, 0, 0, 0.6); }\n.background .ascii-wrapper .ascii {\n      transition: all 400ms ease-in-out;\n      transition-duration: 800ms;\n      -webkit-transform: scale(1);\n              transform: scale(1); }\n.background .ascii-wrapper:hover .ascii {\n      -webkit-transform: scale(1.2);\n              transform: scale(1.2); }\n@media (min-width: 768px) {\n    .background .ascii-wrapper {\n      background-color: rgba(0, 0, 0, 0.4); }\n      .background .ascii-wrapper .ascii {\n        font-size: .28rem; }\n      .background .ascii-wrapper:hover {\n        background-color: rgba(0, 0, 0, 0.6); } }\n@media (min-width: 1024px) {\n    .background {\n      height: 400px; }\n      .background .ascii-wrapper .ascii {\n        -webkit-transform: scale(0.6);\n                transform: scale(0.6);\n        font-size: .65rem; } }\n"